@import"https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap";*,:before,:after{box-sizing:border-box}*{font-family:"Noto Sans KR","Noto+Sans","Malgun Gothic","맑은 고딕",arial,sans-serif;margin:0;padding:0;-webkit-tap-highlight-color:rgba(0,0,0,.1)}#root{overflow:hidden}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section,main{display:block}button{cursor:pointer}ol,ul{list-style:none}address,em{font-style:normal}strong{font-weight:normal}a{text-decoration:none;color:#333;-webkit-tap-highlight-color:rgba(0,0,0,.1)}iframe{overflow:hidden;border:0}table{border-collapse:collapse;border-spacing:0;width:100%}h1,h2,h3,h4,h5,h6{font-weight:normal;font-size:100%}input,select,textarea,button{vertical-align:middle;margin:0;padding:0;border-radius:0;-moz-appearance:none;-webkit-appearance:none;appearance:none;-webkit-tap-highlight-color:rgba(0,0,0,.1)}body{line-height:1.5;-webkit-text-size-adjust:none}